import { useState, useEffect } from "react";
import { useNavigate, Link } from "react-router-dom";
import {
    Box,
    AppBar,
    Drawer,
    IconButton,
    Toolbar,
    Typography,
    List,
    ListSubheader,
    ListItem,
    ListItemIcon,
    ListItemText,
    ListItemButton,
    Divider,
    Collapse,
} from "@mui/material";
import MenuIcon from "@mui/icons-material/Menu";
import CloseIcon from "@mui/icons-material/Close";
import ChecklistIcon from "@mui/icons-material/Checklist";
import ExpandLess from "@mui/icons-material/ExpandLess";
import ExpandMore from "@mui/icons-material/ExpandMore";
import {
    initStorage,
    getStorageConfig,
    getStorageData,
} from "../functions/functions";
import { getApiData } from "../functions/api";

export function HomePage() {
    const [dataState, setDataState] = useState({});
    const [open, setOpen] = useState(false);
    const [openSubmenu1, setOpenSubmenu1] = useState(false);
    const navigate = useNavigate();
    const drawerWidth = 240;

    useEffect(() => {
        document.title = "Inicio";
        initStorage();
        const configData = getStorageConfig();
        if (!configData.loginStatus) return navigate("/config");
        if (!configData.empresaID) return navigate("/empresa");

        const loadData = async () => {
            const response = await getApiData(
                configData.server,
                configData.empresaID
            );
            setDataState(response.data);
            localStorage.setItem("data", JSON.stringify(response.data));
        };

        if (navigator.onLine) {
            console.log("online");
            loadData();
        } else {
            console.log("offline");
            const storageData = getStorageData();
            setDataState(storageData);
        }
    }, []);

    const handleDrawerOpen = () => {
        setOpen(true);
    };

    const handleDrawerClose = () => {
        setOpen(false);
    };

    const handleClickSubmenu1 = () => {
        setOpenSubmenu1(!openSubmenu1);
    };

    return (
        <Box sx={{ display: "flex" }}>
            <AppBar position="fixed">
                <Toolbar variant="dense">
                    <IconButton
                        edge="start"
                        color="inherit"
                        aria-label="open menu"
                        onClick={handleDrawerOpen}
                        sx={{ mr: 2 }}
                    >
                        <MenuIcon />
                    </IconButton>
                    <Typography
                        variant="h6"
                        component="div"
                        sx={{ flexGrow: 1 }}
                    >
                        Inicio
                    </Typography>
                </Toolbar>
            </AppBar>
            <Drawer
                sx={{
                    width: drawerWidth,
                    flexShrink: 0,
                    "& .MuiDrawer-paper": {
                        width: drawerWidth,
                        boxSizing: "border-box",
                    },
                }}
                variant="left"
                anchor="left"
                open={open}
            >
                <List
                    component="nav"
                    aria-labelledby="nested-list-subheader"
                    subheader={
                        <ListSubheader
                            component="div"
                            id="nested-list-subheader"
                        >
                            Men
                            <ListItemButton onClick={handleDrawerClose}>
                                <ListItemIcon>
                                    <CloseIcon />
                                </ListItemIcon>
                            </ListItemButton>
                        </ListSubheader>
                    }
                >
                    <Divider />
                    <ListItemButton onClick={handleClickSubmenu1}>
                        <ListItemIcon>
                            <ChecklistIcon />
                        </ListItemIcon>
                        <ListItemText primary="Auditorias" />
                        {openSubmenu1 ? <ExpandLess /> : <ExpandMore />}
                    </ListItemButton>
                    <Collapse in={openSubmenu1} timeout="auto" unmountOnExit>
                        <List component="div" disablePadding>
                            <ListItemButton
                                component={Link}
                                to="/auditoria/disponible/lista/"
                            >
                                <ListItemIcon>
                                    <ChecklistIcon />
                                </ListItemIcon>
                                <ListItemText primary="Disponibles" />
                            </ListItemButton>
                            <ListItemButton
                                component={Link}
                                to="/auditoria/realizada/lista/"
                            >
                                <ListItemIcon>
                                    <ChecklistIcon />
                                </ListItemIcon>
                                <ListItemText primary="Realizadas" />
                            </ListItemButton>
                        </List>
                    </Collapse>
                    <Divider />
                </List>
            </Drawer>
        </Box>
    );
}
