import { useEffect } from "react";
import { useNavigate } from "react-router-dom";
import { useForm } from "react-hook-form";
import { getStorageConfig } from "../functions/functions";

export function EmpresaPage() {
    const navigate = useNavigate();
    const {
        register,
        handleSubmit,
        formState: { errors },
    } = useForm();
    const configData = getStorageConfig();

    useEffect(() => {
        document.title = "Seleccionar Empresa";
    }, []);

    const onSubmit = handleSubmit((data) => {
        configData.empresaID = parseInt(data.empresa);
        localStorage.setItem("config", JSON.stringify(configData));
        navigate("/");
    });

    return (
        <div>
            <h1>EmpresaPage</h1>
            <form onSubmit={onSubmit}>
                <ul>
                    {configData.unidades.map((unidad) => (
                        <li key={unidad.id}>
                            {unidad.nombre}
                            <ul>
                                {unidad.empresas.map((empresa) => (
                                    <li key={empresa.id}>
                                        <label>
                                            <input
                                                type="radio"
                                                {...register("empresa", {
                                                    required: true,
                                                    valueAsNumber: true,
                                                })}
                                                value={empresa.id}
                                            />
                                            {empresa.nombre}
                                        </label>
                                    </li>
                                ))}
                            </ul>
                        </li>
                    ))}
                </ul>
                {errors.empresa && <span>Debe seleccionar una empresa</span>}
                <br />
                <button type="submit">Seleccionar</button>
            </form>
        </div>
    );
}
