import { useEffect, useState } from "react";
import { useNavigate } from "react-router-dom";
import { useForm } from "react-hook-form";
import { toast } from "react-hot-toast";
import { Backdrop, CircularProgress } from "@mui/material";
import { getApiServers, getApiLogin } from "../functions/api";

export function ConfigPage() {
    const [servers, setServers] = useState([]);
    const [open, setOpen] = useState(false);
    const navigate = useNavigate();
    const {
        register,
        handleSubmit,
        formState: { errors },
    } = useForm();

    useEffect(() => {
        document.title = "Configuracin";
        const loadServer = async () => {
            setOpen(true);
            const response = await getApiServers();
            setServers(response.data);
            setOpen(false);
        };

        loadServer();
    }, []);

    const findServer = (id) => {
        return servers.find((x) => x.id === parseInt(id));
    };

    const onSubmit = handleSubmit(async (data) => {
        const server = findServer(data.server);
        try {
            const response = await getApiLogin(
                server.url,
                data.username,
                data.password
            );
            if (response.data.status) {
                const storageconfig = localStorage.getItem("config");
                const configData = JSON.parse(storageconfig);
                configData.server = server.url;
                configData.loginStatus = true;
                configData.user = response.data.data.user_fullname;
                configData.userID = response.data.data.id;
                configData.unidades = response.data.data.unidades;
                localStorage.setItem("config", JSON.stringify(configData));
                return navigate("/");
            } else {
                toast.error("Usuario o contrasea incorrectos");
            }
        } catch (error) {
            toast.error("Error al conectar con el servidor");
        }
    });

    return (
        <div>
            <form onSubmit={onSubmit}>
                <p>
                    <label htmlFor="server">Servidor:</label>
                    <select {...register("server", { required: true })}>
                        <option value="">----------</option>
                        {servers.map((server) => (
                            <option value={server.id} key={server.id}>
                                {server.url}
                            </option>
                        ))}
                    </select>
                    {errors.server && (
                        <span className="error">Este campo es requerido</span>
                    )}
                </p>
                <p>
                    <label htmlFor="username">Usuario:</label>
                    <input
                        type="text"
                        {...register("username", { required: true })}
                    />
                    {errors.username && (
                        <span className="error">Este campo es requerido</span>
                    )}
                </p>
                <p>
                    <label htmlFor="password">Contrasea:</label>
                    <input
                        type="password"
                        {...register("password", { required: true })}
                    />
                    {errors.password && (
                        <span className="error">Este campo es requerido</span>
                    )}
                </p>
                <button type="submit">Ingresar</button>
            </form>
            <Backdrop
                sx={{
                    color: "#fff",
                    zIndex: (theme) => theme.zIndex.drawer + 1,
                }}
                open={open}
            >
                <CircularProgress color="inherit" />
            </Backdrop>
        </div>
    );
}
